.specific {
  padding: 0;
  border-radius: 10px;
  font-size: 0;
  max-width: 640px;
  display: flex;
  flex-direction: row;
  flex-wrap:  wrap-reverse;

  &__item {
    padding: 30px 15px;
    width: calc(100% / 3);
    border: 1px solid #FAFBFC;
    @media (max-width: 768px) {
      width: calc(73% / 2);
    }
    @media (max-width: 482px) {
      width: 100%;
      padding: 10px;
    }
  }
  &__icon {
    background: #e85352;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    position: relative;
    text-align: center;
    display: inline-block;
    vertical-align: top;
    i {
      font-size: 19px;
      color: #fff;
      line-height: 27px;

    }
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  &__info {
    display: inline-block;
    width: calc(100% - 28px);
    vertical-align: top;
    padding-left: 10px;
  }
  &__title {
    color: #6B6E7A;
    font-size: rem(13);
    font-weight: 300;
  }
  &__value {
    font-size: 1.3rem;
    color: #6B6E7A;
    font-weight: 300;
    @media (max-width: 450px) {
      font-size: rem(14);
    }
  }
}

.specific__header-title {
  position: relative;
  top: 51px;
  display: inline-block;
}

.specific__header-pic{
  display: inline-block;
  width:60px;
}

.specific__header {
  border-bottom: 1px solid #ccc5bb;
}
.specific__header-block {
  margin-left: 3%;
}
.specific-info-block {
  margin-top: 50px;
  @media (max-width: 768px) {
    width: 70%;
  }
  @media (max-width: 650px) {
    width: 100%;
  }
  @media (max-width: 510px) {
    width: 100%;
  }
}

.specific__header-pic {
  display: inline-block!important;
  width: 40px!important;
  opacity: 1;
  padding: 10px!important;
}

.specific__item-title {
  position: relative;
  top: 5px;
  display: inline-block;
}